<template lang="pug">
.page-employee
    my-page-title(title="用户管理")
    .bar
        el-form(:inline="true" @click.native.prevent="")
            el-form-item
                el-input(placeholder="请输入用户姓名")
            el-form-item
                my-button(type="primary" :loading="loading") 搜索
        el-button(type="primary" @click="addEmployee") 新增用户
    .content(v-loading="loading")
        el-table(:data="list" border style="width: 100%")
            el-table-column(align="center" type="index")
            el-table-column(align="center" label="姓名" prop="")
            el-table-column(align="center" label="性别" prop="")
            el-table-column(align="center" label="联系方式" prop="")
            el-table-column(align="center" label="家庭住址" prop="")
            el-table-column(align="center" label="负责模块" prop="")
            el-table-column(align="center" label="操作" prop="")
                template(slot-scope="scope")
                    el-button(type="text") 修改
                    el-button(type="text") 重置密码
                    el-button(type="text") 删除
        .pager
            el-pagination(:page-sizes="[20]"
                layout="total,slot,prev,pager,next"
                :current-page.sync="pagination.curPage"
                :page-size="pagination.pageSize"
                :total="pagination.pageTotal"
                @current-change="pageCurPageChange"
                @size-change="pageSizeChange")
    my-user-editor(ref="my-user-editor")
</template>

<script>
import UserEditor from '../components/UserEditor';
import pageMixin from '@/mixins/page';
export default {
    mixins: [pageMixin],
    data () {
        return {
            list: [{}],
            loading: false
        };
    },
    components: {
        'my-user-editor': UserEditor
    },
    methods: {
        addEmployee () {
            let comp = this.$refs['my-user-editor'];
            comp.open();
        }
    }
};
</script>

<style lang="scss" scoped>
.page-employee {
    .bar {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
    }
}
</style>



